

import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'
 

export default {
    // title: 'Transactions',
    title: {
        singular: 'Transaction',
        plural: 'Transactions',
    },
    icon: 'mdi-bank-transfer',
    withdraw: true,
    currency: 'SAR',
    total: true,
   
    items: {
        total: {
            headers: [
                {
                    text: 'Subtotal',
                    align: 'start',
                    value: 'subtotal.amount',
                    format: 'money',
                    formatArgs: ['SAR'],
                    sortable: false,
                },
                {
                    text: 'Withraw',
                    align: 'start',
                    format: 'money',
                    formatArgs: ['SAR'],
                    value: 'withdraw.amount',
                    sortable: false,
                },
                {
                    text: 'Stuck',
                    align: 'start',
                    format: 'money',
                    formatArgs: ['SAR', false, 'orange--text'],
                    value: 'stuck.amount',
                    sortable: false,
                },
                {
                    text: 'Total',
                    align: 'start',
                    format: 'money',
                    formatArgs: ['SAR'],
                    value: 'amount',
                    sortable: false,
                },
                {
                    text: 'Total USD',
                    align: 'start',
                    format: 'money',
                    value: 'amount_usd',
                    sortable: false,
                }
            ]
        },
        buttons: tableButtons.get('new', 'withdraw', 'Stuck',  'delete'),
        filters: [



            {
                input: 'autocomplete',
                name: 'user',
                if: vm => vm.getPermissions('transaction').write.allOwn == 'all',
                col: {
                    sm: 4
                },
                bind: {
                    label: "Label",
                    prependIcon: "mdi-account",
                    collection: 'user',
                    template: 'user',
                    itemText: "displayName",
                    itemValue: "_id",
                   
                   
                    query: {
                        role: 'wakel'
                    },

                }
            },

            {
                input: 'range',
                name: 'created_on',
                col: {
                    sm: 5
                },
                default: (new Date()).toISOString().split('T')[0],
                bind: {
                    label: "Date",
                    prependIcon: "mdi-calendar",

                }
            },
            {
                input: 'select',
                name: 'type',
                col: {
                    sm: 3
                },
                bind: {
                    label: "Type",
                    items:[
                        {
                            text: 'Deposit',
                            value: 'deposit'
                        },
                        {
                            text: 'Withdraw',
                            value: 'wtd'
                        },
                        {
                            text: 'Stuck',
                            value: 'stk'
                        }
                    ],
                    prependIcon: "mdi-format-list-bulleted-type",
                    clearable: true

                }
            }

        ],
        headers: [
            { text: 'Account name', value: 'name', class: "font-weight-bold" },
            { text: 'Type', sortable: false, value: 'type', format: 'depositType', width: '50'  },
            { text: 'Amount', value: 'amount', format: 'money', formatArgs: ['SAR'] },
            // { text: 'Email', value: 'email' },
            { text: 'Date', value: 'created_on', format: 'date' },
            { 
                text: 'Label', 
                sortable: false, 
                value: 'user.displayName',
                width: '200',
                component: () => import('./../components/items/fields/User.vue')
            },
            { text: 'profileImage', sortable: false, value: 'user.profileImage', hide: true },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }
        ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar:{
            panels: [
                {
                    title: "Created Date",
                    fields: [
                        {
                            input: 'date',
                            name: 'created_on',
                            default: (new Date()).toISOString().split('T')[0],
                            bind: {
                                label: "Date",
                                prependInnerIcon: 'mdi-calendar',
                                required: true,
                                rules: [
                                    v => !!v || 'Created On is required',
                                ],
                            }
                        },
                    ]
                },
                {
                    title: 'Label',
                    icon: 'mdi-account',
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'transaction')[0].write.allOwn == 'all'),
                            // default: vm => vm.$store.state.auth.user._id,
                            bind: {
                                label: "Label",
                                // prependIcon: "mdi-account",
                                collection: 'user',
                                template: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                required: true,
                                or: false,
                                query: {
                                    role: 'wakel'
                                },
                                rules: [
                                    v => !!v || 'Label is required',
                                ],
                            }
                        },
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Account name',
                    icon: 'mdi-account-box',
                    fields:[
                        {
                            input: 'text',
                            name: 'name',
                            bind: {
                                label: "Account name",
                                // prependIcon: "mdi-account-box",

                            }
                        },
                    ]
                },
                {
                    title: 'Payment',
                    fields: [
                        {
                            input: 'text',
                            name: 'amount',

                            bind: {
                                label: "Amount",
                                prependInnerIcon: 'mdi-currency-rial',
                                required: true,
                                type: 'number',
                                rules: [
                                    v => !!v || 'Amount is required',
                                ],
                            }
                        },


                        {
                            input: 'hidden',
                            name: 'type',
                            default() {
                                const urlParams = new URLSearchParams(location.search);
                                return urlParams.get('type') || 'deposit'
                            }
                        },
                    ]
                }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Account name",
                        prependIcon: "mdi-account-box",
                     
                    }
                },
                {
                    input: 'text',
                    name: 'amount',
                   
                    bind: {
                        label: "Amount",
                        prependIcon: 'mdi-currency-rial',
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    }
                },
               
             
                {
                    input: 'hidden',
                    name: 'type',
                    default () {
                        const urlParams = new URLSearchParams(location.search);
                        return urlParams.get('type') || 'deposit'
                    }
                },
                {
                    input: 'date',
                    name: 'created_on',
                    default: (new Date()).toISOString().split('T')[0],
                    bind: {
                        label: "Created Date",
                        prependIcon: 'mdi-calendar',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Created On is required',
                        ],
                    }
                },

                 
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'transaction')[0].write.allOwn == 'all'),
                    bind: {
                        label: "Label",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        required: true,
                        or: false,
                        query: {
                            role: 'wakel'
                        },
                        rules: [
                            v => !!v || 'Label is required',
                        ],
                    }
                },
                
               
            ]
        }
    ]
}