import get from './getMethod'

export default {
    get,
    buttons: [
        {
            title: 'Save',
            icon: 'mdi-content-save',
            bind: vm =>  ({
                color: 'info',
                loading: vm.loading && vm.clickedButton == 'save',
                disabled: vm.loading

            }),
            on: vm => ({
                async click() {
                   
                    const { form } = vm.$refs    
                    if (form.validate()) {
                        const data = { collection: vm.collectionName, data: vm.data }
                        if (!vm.isNew)
                            data.id = vm.id

                        vm.loading = true
                        vm.clickedButton = 'save'
                        
                        const result = vm.isNew ? await vm.save(data) : await vm.update(data)

                        vm.loading = false
                        delete vm.clickedButton


                        // update the cached state with the new field value
                        const { backURL, field } = vm.$route.query
                        if (backURL && field) {
                            const cache = localStorage.getItem('backURL')
                            if (cache) {
                                const { cachState, backURL: bURL   } = JSON.parse(cache)
                                if (backURL ==  bURL) {
                                    //set the new field value
                                    cachState.data[field] = result.data
                                    // save the cache
                                    localStorage.setItem('backURL', JSON.stringify({
                                        backURL,
                                        cachState
                                    }))

                                }
                            }
                        }
                            
                        if (result.status == 200)
                            vm.$router.go(-1);

                        vm.$store.commit('success', `${vm.title} has been ${!vm.isNew ? ' updated' : ' saved'} successfuly`)

                    } else 
                        vm.$store.commit('error', 'One or more fields is not valid. please check the form and try again')
                   
                }
            }) 
        },
        {
            title: 'Cancel',
            icon: 'mdi-content-save-off',
            bind: vm => ({
                
                disabled: vm.loading

            }),
            on: vm => ({
                click () {
                    vm.$router.go(-1)
                }
            })
             
        },
        {
            title: 'Delete',
            icon: 'mdi-delete',
            if: vm => !vm.isNew && vm.canDelete,
            bind: (vm) => ({
                color: 'red',
                loading: vm.loading && vm.clickedButton == 'delete',
                disabled: vm.loading
            }),
            on: (vm) => ({
                async click() {
               
            
                    const confirmed = await vm.$confirm(`Do you realy want to delete this item?`, { title: 'Delete Item', color: 'red' })
                    if (confirmed) {
                        vm.loading = true
                        vm.clickedButton = 'delete'

                        const rm = await vm.remove({ collection: vm.$route.params.collection, id: vm.id })
                        if (rm.status === 200) {
                            vm.$store.commit('success', `item has been deleted successfuly`)
                            vm.$router.go(-1)
                        }
                        vm.loading = false
                        delete vm.clickedButton
                    }
                    // console.log(confirmed);
                }
            })
        },
         
    ]
}