
 
import tableButtons from './defaults/items_buttons'
import itemButtons from './defaults/item_buttons'
import rowButtons from './defaults/row_buttons'
 
export default {
    // title: 'Deposits',
    title: {
        singular: 'Deposit',
        plural: 'Deposits',
    },
    icon: 'mdi-bank-transfer-in',
    items: {
        total: {
            headers: [
                {
                    text: 'Subtotal',
                    align: 'start',
                    value: 'subtotal.amount',
                    format: 'money',
                    sortable: false,
                },
                {
                    text: 'Withraw',
                    align: 'start',
                    format: 'money',
                    value: 'withdraw.amount',
                    sortable: false,
                },
                {
                    text: 'Total',
                    align: 'start',
                    format: 'money',
                    value: 'amount',
                    sortable: false,
                }
            ]
        },
        buttons: tableButtons.get('new', 'withdraw', 'delete' ),
       
        filters: [
            {
                input: 'autocomplete',
                name: 'customer',
                col: {
                    sm: 6
                },
                bind: {
                    label: "Account",
                    template: 'customer',
                    returnObject: false,
                    prependIcon: "mdi-account-box",
                    collection: 'customer',
                    itemText: "name",
                    itemValue: "_id",
                    searchFields: ['name', 'tpc'],
                    hideAddNew: true,

                }
            },
            {
                input: 'range',
                name: 'created_on',
                col: {
                    sm: 6
                },
                bind: {
                    label: "Date",
                    prependIcon: "mdi-calendar",

                }
            },
        {
            input: 'autocomplete',
            name: 'business',
            if: vm => !(vm.user.business),
            col: {
                sm: 4
            },
            bind: {
                label: "Business Unit",
                prependIcon: "mdi-domain",
                collection: 'business',
                itemText: "name",
                itemValue: "_id",
          

            }
        },
        {
            input: 'autocomplete',
            name: 'department',
            if: vm => !(vm.user.department),
             
            col: {
                sm: 4
            },
            bind: {
                label: "Department",
                prependIcon: "mdi-briefcase-account-outline",
                collection: 'department',
                itemText: "name",
                itemValue: "_id",
            


            },
            on: vm => ({
                input(evt) {
                    const userField = `filter_${vm.collectionName}_user`
                    if (evt)
                        vm.props[userField] = { 
                            query:{
                                department: evt
                            }
                        }
                    else
                        delete vm.props[userField]
                }
            })
        },
        {
            input: 'autocomplete',
            name: 'user',
            if: vm => (vm.getPermissions('deposit').write.allOwn == 'all'),
            col: {
                sm: 4
            },
            bind: {
                label: "Owner",
                template: 'user',
                prependIcon: "mdi-account",
                collection: 'user',
                itemText: "displayName",
                itemValue: "_id",
                hideAddNew: true,
                or: false,
                query: {
                    role: ['!wakel', '!affiliate']
                }

            }
        },
        

        ],
        headers: [
            {
                text: 'TP Account',
                align: 'start',
                value: 'customer.tpc',
                sortable: false,
            },
            { text: 'Account name', sortable: false, value: 'customer.name' },
            { text: 'Country', sortable: false, value: 'customer.country', format: 'country', width: '150' },
            { text: 'Type', sortable: false, value: 'type', format: 'depositType', width: '50' },
            { text: 'Amount', value: 'amount', format: 'money' },
            { text: 'Payment Method', sortable: false, value: 'payment_method.name' },
            { text: 'Affiliate', value: 'customer.affiliate.name' },
            { text: 'Sub Affiliate', value: 'customer.subaffiliate.name' },
            { text: 'Created Date', value: 'created_on', width: '120', format: 'date' },
            { text: 'Note', value: 'note' },
            { text: 'Business Unit', value: 'business.name', sortable: false },
            { text: 'Department', value: 'department.name', sortable: false },
            { 
                text: 'Owner', 
                sortable: false, 
                value: 'user.displayName', 
                width: '200',
                component: () => import('./../components/items/fields/User.vue') 
            },
            { text: 'profileImage', sortable: false, value: 'user.profileImage', hide: true },
            { 
                text: '', 
                value: 'actions', 
                sortable: false, 
                width: '150',
                buttons: rowButtons.get() 
            }
            ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels: [
                {   
                    title: "Created Date",
                    fields: [
                        {
                            input: 'date',
                            name: 'created_on',
                            default: (new Date()).toISOString().split('T')[0],
                            bind: {
                                label: "Date",
                                prependInnerIcon: 'mdi-calendar',
                                required: true,
                                rules: [
                                    v => !!v || 'Created On is required',
                                ],
                            }
                        },
                    ]
                },
                {
                    title: 'Business',
                    // icon: 'mdi-domain',
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'business',
                            if: vm => !(vm.user.business),
                            default: vm => vm.user.business,
                            bind: {
                                label: "Business Unit",
                                prependInnerIcon: "mdi-domain",
                                collection: 'business',
                                itemText: "name",
                                itemValue: "_id",
                                required: true,
                                rules: [
                                    v => !!v || 'Business Unit is required',
                                ],
                            }
                        },
                        {
                            input: 'autocomplete',
                            name: 'department',
                            if: vm => !(vm.user.department),
                            default: vm => vm.user.department,
                            bind: () => ({
                                label: "Department",
                                prependInnerIcon: "mdi-briefcase-account-outline",
                                collection: 'department',
                                itemText: "name",
                                itemValue: "_id",
                                required: true,
                                rules: [
                                    v => !!v || 'Department is required',
                                ]
                            }),
                            on: (vm) => ({
                                input(evt) {
                                    const fieldName = vm.collectionName + '_' + 'user'
                                    if (evt) {
                                        vm.props[fieldName] = {
                                            query: {
                                                department: evt,
                                                role: ['!wakel', '!affiliate']
                                            }
                                        }
                                        
                                    } else if (vm.props[fieldName]) {
                                        delete vm.props[fieldName]
                                    }
                                }
                            })
                        }
                    ]
                },
                {
                    title: 'Owner',
                    icon: 'mdi-account',
                   
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'deposit')[0].write.allOwn == 'all'),
                            default: vm => vm.user,
                            bind: (vm) => ({
                                label: "Owner",
                                // prependInnerIcon: "mdi-account",
                                template: 'user',
                                collection: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                required: true,
                                rules: [
                                    v => !!v || 'Owner is required',
                                ],
                                or: false,
                                query: (() => {
                                    return vm.data.department ? { department: vm.data.department, role: ['!wakel', '!affiliate'] } : { role: ['!wakel', '!affiliate'] }
                                })() 
                            })
                        },
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Account',
                    icon: 'mdi-account-box',
                    buttons: [
                        {
                            title: 'Add New Account',
                            if: vm => (vm.getPermissions('customer').write.value && vm.isNew),
                            icon: 'mdi-account-plus',
                            bind: {
                                color: 'green'
                            },
                            on: vm => ({
                                click () {
                                    const backURL = btoa(vm.$route.path)
                                    const cachState = Object.assign({}, vm.$data)
                                    localStorage.setItem('backURL', JSON.stringify({
                                        backURL,
                                        cachState
                                    }))
                                    
                                    vm.$router.push({
                                        path: '/customer/new',
                                        query: {
                                            field: 'customer',
                                            backURL: btoa(vm.$route.path)
                                        }
                                    })
                                }
                            })
                        }
                    ],
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'customer',
                            bind: () => ({
                                label: "Account",
                            
                                template: 'customer',
                                collection: 'customer',
                                itemText: "name",
                                itemValue: "_id",
                                required: true,
                                searchFields: ['tpc'],

                                rules: [
                                    v => !!v || 'Account is required',
                                ],
                            })
                        },
                    ]
                },
                {
                    title: 'Payment',
                   
                    fields: [
                        {
                            input: 'text',
                            name: 'amount',
                           
                            bind: {
                                label: "Amount",
                                prependInnerIcon: 'mdi-currency-usd',
                                required: true,
                                type: 'number',
                                rules: [
                                    v => !!v || 'Amount is required',
                                ],
                            }
                        },

                        {
                            input: 'autocomplete',
                            name: 'payment_method',
                           
                            if() {
                                // hide on withdraw
                                const urlParams = new URLSearchParams(location.search);
                                return urlParams.get('type') != 'wtd'
                            },
                            default: () => null,
                            bind: {
                                label: "Payment Method",
                                prependInnerIcon: "mdi-credit-card-settings-outline",
                                collection: 'payment_method',
                                itemText: "name",
                                itemValue: "_id",
                                required: true,


                                rules: [
                                    v => !!v || 'Payment Method is required',
                                ],
                            }
                        },
                        {
                            input: 'hidden',
                            name: 'type',
                            default() {
                                const urlParams = new URLSearchParams(location.search);
                                return urlParams.get('type')
                            }
                        },
                    ]
                },
                {
                    title: 'Note',
                    icon: 'mdi-message',
                    fields: [
                        {
                            input: 'textarea',
                            name: 'note',
                            bind: {
                                placeholder: "Write a note..."
                            }
                        },
                    ]
                }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'autocomplete',
                    name: 'customer',
                    bind: {
                        label: "Account",
                        prependIcon: "mdi-account-box",
                        collection: 'customer',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        searchFields: ['name', 'tpc'],

                        rules: [
                            v => !!v || 'Account is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'amount',

                    bind: {
                        label: "Amount",
                        prependIcon: 'mdi-cash',
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    }
                },

                {
                    input: 'autocomplete',
                    name: 'payment_method',
                    if() {
                        // hide on withdraw
                        const urlParams = new URLSearchParams(location.search);
                        return urlParams.get('type') != 'wtd'
                    },
                    default: () =>  null,
                    bind: {
                        label: "Payment Method",
                        prependIcon: "mdi-credit-card-settings-outline",
                        collection: 'payment_method',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,


                        rules: [
                            v => !!v || 'Payment Method is required',
                        ],
                    }
                },
                {
                    input: 'hidden',
                    name: 'type',
                    default () {
                        const urlParams = new URLSearchParams(location.search);
                        return urlParams.get('type')  
                    }
                },
                {
                    input: 'date',
                    name: 'created_on',
                    default: (new Date()).toISOString().split('T')[0],
                    bind: {
                        label: "Created Date",
                        prependIcon: 'mdi-calendar',
                        required: true,


                        rules: [
                            v => !!v || 'Created On is required',
                        ],
                    }
                },
                {
                    input: 'textarea',
                    name: 'note',
                    bind: {
                        label: "Note",
                        prependIcon: 'mdi-message',
                        outlined:true
                    }
                },
                // {
                //     input: 'select',
                //     name: 'type',
                //     bind: {
                //         label: "Deposit Type",
                //         prependIcon: 'mdi-bank-transfer',
                //         items: [
                //             { text: 'First Deposit', value: 'ftd' }, 
                //             { text: 'Retention Deposit', value: 'rtn' },
                //             { text: 'Withdraw', value: 'wtd' },
                //         ],
                //         required: true,


                //         rules: [
                //             v => !!v || 'Created On is required',
                //         ],
                //     }
                // },
                {
                    input: 'autocomplete',
                    name: 'business',
                    if: state => !(state.auth.user.business),
                    default: state => state.auth.user.business,
                    bind: {
                        label: "Business Unit",
                        prependIcon: "mdi-domain",
                        collection: 'business',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Business Unit is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'department',
                    if: state => !(state.auth.user.department),
                    default: state => state.auth.user.department,
                    bind: {
                        label: "Department",
                        prependIcon: "mdi-briefcase-account-outline",
                        collection: 'department',
                        itemText: "name",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Department is required',
                        ],
                    },
                    on: () => ({
                        input(evt) {
                            const { deposit_user } = window.$form.$refs
                            if (!evt && deposit_user[0].query.department)
                                delete deposit_user[0].query.department
                            else if (evt)
                                deposit_user[0].query.department = evt

                        }
                    })
                },

                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'deposit')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                        label: "Owner",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Owner is required',
                        ],
                        or: false,
                        query: {

                            role: ['!wakel', '!affiliate']
                        }
                    }
                },


            ]
        }
    ]

}


