import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default {
    title: {
        singular: 'Department',
        plural: 'Departments',
    },
    icon: 'mdi-briefcase-account-outline',
    items: {
        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Name',
                align: 'start',
                sortable: false,
                value: 'name',
            },
            {
                text: 'Deposit Type',
                sortable: false,
                value: 'type',
            },
            { text: '', value: 'actions', width: '150', buttons: rowButtons.get() }
        ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels: [
                {
                    title: 'Created By',
                    icon: "mdi-account",
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'department')[0].write.allOwn == 'all'),
                            default: vm => vm.user,
                            bind: {
                                label: "User",
                                template: 'user',
                                collection: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                hideAddNew: true,
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
               {
                   title: 'Department',
                    fields: [
                        {
                            input: 'text',
                            name: 'name',
                            col: {
                                md: 8
                            },
                            bind: {
                                label: "Name",
                                rules: [
                                    v => !!v || 'Name is required',
                                ],
                                required: true,
                            }
                        },
                        {
                            input: 'select',
                            name: 'type',
                            col: {
                                md: 4
                            },
                            bind: {
                                label: "Deposit Type",
                                items: [
                                    { text: 'FTD', value: 'ftd' },
                                    { text: 'Retention', value: 'rtn' },
                                    { text: 'Withdraw', value: 'wtd' },
                                ],
                                rules: [
                                    v => !!v || 'Deposit Type',
                                ],
                                required: true,
                            }
                        }
                    ]
               }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'select',
                    name: 'type',
                    bind: {
                        label: "Deposit Type",
                        items: [
                            { text: 'FTD', value: 'ftd' },
                            { text: 'Retention', value: 'rtn' },
                            { text: 'Withdraw', value: 'wtd' },
                        ],
                        rules: [
                            v => !!v || 'Deposit Type',
                        ],
                        required: true,
                    }
                }
            ]
        }
    ]
}