import get from './getMethod'
export default {
    get,
    buttons: [
        {
            title: 'Edit',
            icon: 'mdi-pencil',
            if: (vm) => vm.canUpdate,
            bind: {
                color: 'info'
            },
            on: (vm, item) => ({
                click () {
                    vm.$router.push(vm.$route.path + '/' + item._id)
                }
            })
        },
        {
            title: 'Delete',
            icon: 'mdi-delete',
            if: (vm) => vm.canDelete,
            bind: {
                color: 'red'
            },
            on: (vm, item) => ({
                async click() {
                    const confirmed = await vm.$confirm('Are you sure do you want to delete this item ?', { title: 'Delete Item', color: 'red' })
                    if (confirmed) {
                        vm.loading = true
                        const rm = await vm.remove({ collection: vm.$route.params.collection, id: item._id })
                        if (rm.status === 200) {

                            vm.fetch()
                            vm.loading = false
                            vm.$store.commit('success', 'Item has been deleted successfuly')
                        }
                    }
                }
            })
        },
        {
            title: 'Duplicate',
            icon: 'mdi-content-copy',
            if: (vm) => vm.canWrite,
            on: (vm, item) => ({
                click() {
                    vm.$router.push(vm.$route.path + '/new?clone=' + item._id)
                }
            })
        }
    ]
}