import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default  {
        // title: 'Users',
        title: {
            singular: 'User',
            plural: 'Users',
        },
        icon: 'mdi-account-multiple',
        
        items: {
            // buttons: [],
            buttons: tableButtons.get('new', 'delete'),
            filters: [
                {
                    input: 'text',
                    name: 'displayName',
                    search: 'username', 
                    col: {
                        sm: 6
                    },
                    bind: {
                        label: "Display Name / Username",
                        prependIcon: 'mdi-account',

                    }
                },
                {
                    input: 'autocomplete',
                    name: 'role',
                    col: {
                        sm: 3
                    },
                    bind: {
                        label: "Role",
                        collection: 'role',
                        itemText: "name",
                        itemValue: "role",
                        prependIcon: "mdi-key",

                    }
                },
                {
                    input: 'autocomplete',
                    name: 'department',
                    col: {
                        sm: 3
                    },
                    bind: {
                        label: "Department",
                        prependIcon: "mdi-briefcase-account-outline",
                        collection: 'department',
                        itemText: "name",
                        itemValue: "_id",
                    }
                },
            ],
            headers: [
                {
                    text: 'Pic',
                    align: 'start',
                    value: 'profileImage',
                    component: () => import('./../components/items/fields/Pic')
                },
                {
                  text: 'Display Name',
                  align: 'start',
                  
                  value: 'displayName',
                },
                { text: 'Username', value: 'username' },
                { text: 'Role', value: 'role' },
                { text: 'Target', value: 'target' },
                { text: 'Business Unit', sortable: false, value: 'business.name' },
                { text: 'Department', sortable: false, value: 'department.name' },
                { text: 'Town', value: 'town' },
                { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() } 
            ]
        },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
            sidebar: {
                panels: [
                    {
                        title: 'Role',
                        icon: 'mdi-key',
                        fields: [
                            {
                                input: 'autocomplete',
                                name: 'role',
                                bind: {
                                    label: "Role",
                                    collection: 'role',
                                    itemText: "name",
                                    itemValue: "role",
                                    // prependIcon: "mdi-key",
                                    required: true,
                                    rules: [
                                        v => !!v || 'Role is required',
                                    ],
                                }
                            },
                        ]
                    },
                    {
                        title: 'Target',
                        icon: 'mdi-bullseye-arrow',
                        fields: [
                            {
                                input: 'text',
                                name: 'target',
                                default: 0,
                                bind: {
                                    label: "Target",
                                    // prependIcon: "mdi-bullseye-arrow",
                                    type: 'number'
                                }
                            }, 
                        ]
                    },
                   
                    {
                        title: 'Business',
                        fields: [
                            {
                                input: 'autocomplete',
                                name: 'business',
                                default: (vm) => vm.user.business,
                                if: (vm) => !vm.user.business,
                                bind: {
                                    label: "Business Unit",
                                    prependInnerIcon: "mdi-domain",
                                    collection: 'business',
                                    itemText: "name",
                                    itemValue: "_id",
                                }
                            },
                            {
                                input: 'autocomplete',
                                name: 'department',
                                default: (vm) => vm.user.department,
                                if: (vm) => !vm.user.department,
                                bind: vm => ({
                                    label: "Department",
                                    prependInnerIcon: "mdi-briefcase-account-outline",
                                    collection: 'department',
                                    itemText: "name",
                                    itemValue: "_id",
                                    returnObject: true,
                                    hint: 'Deposit Type ' + vm.data.depositType
                                }),
                                on: vm => ({
                                    input (data) {
                                        vm.data.depositType = data.type
                                    }
                                })
                            },
                            {
                                name: 'depositType',
                            }
                        ]
                    },
                    {
                        title: 'Affiliate',
                        fields: [
                            {
                                input: 'autocomplete',
                                if: (vm) => ['admin', 'owner'].indexOf(vm.user.role) != -1,
                                name: 'affiliate',
                                bind: {
                                    label: "Affiliate",
                                    prependInnerIcon: "mdi-account-network-outline",
                                    collection: 'affiliate',
                                    itemText: "name",
                                    itemValue: "_id",
                                }
                            },
                        ]
                    }
                    
                ]
            },
            content: {
                panels: [
                    {
                        title: 'Display',
                        fields: [
                            {
                                input: 'profile-image',
                                name: 'profileImage',
                                bind: {
                                    placeholder: "Profile Picture",
                                    prependIcon: 'mdi-image',

                                }
                            },
                            {
                                input: 'text',
                                name: 'displayName',
                                bind: {
                                    label: "Display Name",
                                    prependInnerIcon: 'mdi-account',
                                    required: true,
                                    rules: [
                                        v => !!v || 'Display Name is required',
                                    ],
                                }
                            },
                        ]
                    },
                    {
                        title: 'Address',
                        fields: [
                            {
                                input: 'select',
                                name: 'town',
                                bind: {
                                    label: "Town",
                                    items: ['Haifa', 'Nabless'],
                                    prependInnerIcon: "mdi-map-marker",
                                    required: true,
                                    rules: [
                                        v => !!v || 'Town is required',
                                    ],
                                }
                            },
                        ]
                    },
                    {
                        title: 'Username & Password',
                        fields: [
                            {
                                input: 'text',
                                name: 'username',
                                bind: {
                                    label: "Username",
                                    autocomplete: 'username',
                                    prependIcon: 'mdi-account-box',
                                    required: true,
                                    rules: [
                                        v => !!v || 'Username is required',
                                    ],
                                }
                            },
                            {
                                input: 'password',
                                name: 'password',
                                bind: {
                                    label: "Password",
                                    prependIcon: 'mdi-lock',


                                }
                            },
                        ]
                    }
                ]
            }
        },
        form: [
            {
                label: 'General',
                fields: [
                    {
                        input: 'profile-image',
                        name: 'profileImage',
                        bind: {
                            placeholder: "Profile Picture",
                            prependIcon: 'mdi-image',

                        }
                    },
                    {
                        input: 'text',
                        name: 'displayName',
                        bind: {
                            label: "Display Name",
                            prependIcon: 'mdi-account',
                            required: true,
                            rules: [
                                v => !!v || 'Display Name is required',
                            ],
                        } 
                    },
              
                    {
                        input: 'text',
                        name: 'username',
                        bind: {
                            label: "Username",
                            autocomplete: 'username',
                            prependIcon: 'mdi-account-box',
                            required: true,
                            rules: [
                                v => !!v || 'Username is required',
                            ],
                        } 
                    },
                    {
                        input: 'password',
                        name: 'password',
                        bind: {
                            label: "Password",
                            prependIcon: 'mdi-lock',
                             
                            
                        } 
                    },
                    {
                        input: 'select',
                        name: 'town',
                        bind: {
                            label: "Town", 
                            items: ['Haifa', 'Nabless'],
                            prependIcon: "mdi-map-marker",
                            required: true,
                            rules: [
                                v => !!v || 'Town is required',
                            ],
                        } 
                    }, 
                    {
                        input: 'autocomplete',
                        name: 'role',
                        bind: {
                            label: "Role",
                            collection: 'role',
                            itemText: "name",
                            itemValue: "role",
                            prependIcon: "mdi-key",
                            required: true,
                            rules: [
                                v => !!v || 'Role is required',
                            ],
                        }
                    },
                    {
                        input: 'text',
                        name: 'target',
                        bind: {
                            label: "Target",
                            prependIcon: "mdi-bullseye-arrow",
                            type: 'number'
                        }
                    }, 
                    {
                        input: 'autocomplete',
                        name: 'business',
                        default: (state) => state.auth.user.business,
                        if: (state) => !state.auth.user.business,
                        bind: {
                            label: "Business Unit",
                            prependIcon: "mdi-domain",
                            collection: 'business',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'autocomplete',
                        name: 'department',
                        default: (state) => state.auth.user.department,
                        if: (state) => !state.auth.user.department,
                        bind: {
                            label: "Department",
                            prependIcon: "mdi-briefcase-account-outline",
                            collection: 'department',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'autocomplete',
                        if: (state) => ['admin', 'owner'].indexOf(state.auth.user.role) != -1,
                        name: 'affiliate',
                        bind: {
                            label: "Affiliate",
                            prependIcon: "mdi-account-network-outline",
                            collection: 'affiliate',
                            itemText: "name",
                            itemValue: "_id",
                        }
                    },
                    {
                        input: 'switch',
                        name: 'active',
                        default: true,
                        bind: {
                            label: "Active" 
                        } 
                    }
                ]
            }
        ]
    }