import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default {
        // title: 'Roles',
        title: {
            singular: 'Role',
            plural: 'Roles',
        },
        icon: 'mdi-key',
        items: {
            buttons: tableButtons.get('new', 'delete'),
            headers: [
                {
                    text: 'Display Name',
                    align: 'start',
                    sortable: false,
                    value: 'name',
                },
                { text: 'Role', value: 'role' },
                { text: 'Menu', value: 'menu.name' },
                { text: '', value: 'actions', width: '150', buttons: rowButtons.get() }

            ],
        },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
            sidebar: {
                panels: [
                   
                    {
                        title: 'Menu',
                        fields: [
                            {
                                input: 'autocomplete',
                                name: 'menu',
                                bind: {
                                    label: "Menu",
                                    prependInnerIcon: "mdi-menu",
                                    collection: 'menu',
                                    itemText: "name",
                                    itemValue: "_id",
                                    // required: true,


                                    // rules: [
                                    //     v => !!v || 'Menu is required',
                                    // ],
                                }
                            },
                        ]
                    },
                    {
                        title: 'Created By',
                        icon: "mdi-account",
                        fields: [
                            {
                                input: 'autocomplete',
                                name: 'user',
                                if: vm => (vm.user.rights.filter(e => e.collection == 'role')[0].write.allOwn == 'all'),
                                default: vm => vm.user,
                                bind: {
                                    label: "User",
                                    template: 'user',
                                    collection: 'user',
                                    itemText: "displayName",
                                    itemValue: "_id",
                                    hideAddNew: true,
                                }
                            }
                        ]
                    }
                ]
            },
            content: {
                panels: [
                    {
                        title: 'Role',
                        fields: [
                            {
                                input: 'text',
                                name: 'name',
                                bind: {
                                    label: "Role Name",
                                    prependIcon: 'mdi-label',
                                    required: true,

                                    rules: [
                                        v => !!v || 'Display Name is required',
                                    ],
                                },
                                on: () => ({
                                    input(v) {
                                        const role = v.replace(/[^0-9a-zA-Z\s+]+/, '').replace(/\s+/g, '_').toLowerCase()
                                        window.$form.data.role = role

                                    }
                                })
                            },
                            {
                                input: 'text',
                                name: 'role',
                                bind: {
                                    label: "Role Alias",

                                    autocomplete: 'username',
                                    prependIcon: 'mdi-key',
                                    required: true,
                                    rules: [
                                        v => !!v || 'Role Alias is required',
                                    ],
                                }
                            },
                        ]
                    },
                    {
                        // title: 'Permissions',
                        fields:[
                            {
                                input: 'rights',
                                name: 'rights',
                                default: [],
                                bind: {
                                    label: "Permissions",
                                }
                            }
                        ]
                    }
                ]
            }
        },
        form: [
            {
                label: 'General',
                fields: [
                    {
                        input: 'text',
                        name: 'name',
                        bind: {
                            label: "Role Name",
                            prependIcon: 'mdi-label',
                            required: true,
                            
                            rules: [
                                v => !!v || 'Display Name is required',
                            ],
                        },
                        on: () => ({
                            input(v) {
                                const role = v.replace(/[^0-9a-zA-Z\s+]+/, '').replace(/\s+/g, '_').toLowerCase()
                                window.$form.data.role = role 
                               
                            }
                        })
                    },
                    {
                        input: 'text',
                        name: 'role',
                        bind: {
                            label: "Role Alias",
                     
                            autocomplete: 'username',
                            prependIcon: 'mdi-key',
                            required: true,
                            rules: [
                                v => !!v || 'Role Alias is required',
                            ],
                        }
                    },
                    {
                        input: 'autocomplete',
                        name: 'menu',
                        bind: {
                            label: "Menu",
                            prependIcon: "mdi-menu",
                            collection: 'menu',
                            itemText: "name",
                            itemValue: "_id",
                            // required: true,


                            // rules: [
                            //     v => !!v || 'Menu is required',
                            // ],
                        }
                    },
                    {
                        input: 'rights',
                        name: 'rights',
                        default: [],
                        bind: {
                            label: "Permissions",
                        }
                    } 
                ]
            } 
        ]
    }