import get from './getMethod'
export default {
    get,
    buttons: [
        {
            title: 'New',
            icon: 'mdi-plus',
            if: (vm) => vm.canWrite,
            bind: {
                color: 'green',
            },
            on: (vm) => ({
                click() {
                    vm.$router.push(vm.$route.path + '/new')
                }
            })
        },
        {
            title: 'Withdraw',
            icon: 'mdi-bank-transfer-out',
            if: (vm) => vm.canWrite,
            bind: {
                color: 'red',
            },
            on: (vm) => ({
                click() {
                    vm.$router.push(vm.$route.path + '/new?type=wtd')
                }
            })
        },
        {
            title: 'Stuck',
            icon: 'mdi-bank-off',
            if: (vm) => vm.canWrite,
            bind: {
                color: 'orange',
            },
            on: (vm) => ({
                click() {
                    vm.$router.push(vm.$route.path + '/new?type=stk')
                }
            })
        },
        {
            title: 'Delete',
            icon: 'mdi-delete',
            if: (vm) => vm.canDelete,
            bind: (vm) => ({
                color: 'red',
                disabled: vm.selected.length < 1
            }),
            on: (vm) => ({
                async click() {
                    if (vm.selected.length) {
                        const confirmed = await vm.$confirm(`Do you realy want to delete <b>(${vm.selected.length})</b> items ?`, { title: 'Delete Item', color: 'red' })
                        if (confirmed) {
                            vm.loading = true
                            const rm = await vm.remove({ collection: vm.$route.params.collection, id: 'delete', data: vm.selected.map(item => item._id) })
                            if (rm.status === 200) {

                                vm.fetch()
                                vm.loading = false
                                vm.$store.commit('success', `(${vm.selected.length}) items has been deleted successfuly`)
                                vm.selected = []
                            }
                        }
                        console.log(confirmed);
                    }
                }
            })
        },
        {
            title: 'Assign',
            icon: 'mdi-clipboard-account',
            if: (vm) => vm.canUpdate,
            bind: (vm) => ({
                color: 'info',
                disabled: vm.selected.length < 1
            }),
            on: (vm) => ({
                async click() {
                    if (vm.selected.length) {
                        vm.dialog.value = true
                        vm.dialog.bind = {
                            'max-width': "650",
                            scrollable: true
                        }
                        vm.dialog.component = {
                            is: () => import('./../../components/items/dialog/Assign.vue'),
                            bind: (vm) => ({
                                items: vm.selected,
                                headers: [
                                    { text: 'TP Account', value: 'tpc' },
                                    { text: 'Account Name', value: 'name' },
                                    { text: 'Remove', value: 'action' },
                                ]
                            }),
                            on: (vm) => ({
                                close () {
                                    vm.dialog.value = false
                                },
                                unselect (item) {
                                    vm.selected = vm.selected.filter(e => item != e)
                                  
                                },
                                async save (user) {
                                    if (vm.selected.length) {
                                        const update = await vm.update({
                                            collection: 'customer', id: 'assign', data: {
                                                user: user,
                                                items: vm.selected.map(e => e._id)
                                            }
                                        })

                                        if (update.status == 200) {
                                            vm.$store.commit('success', update.data.nModified + ' items has been assigned')
                                            vm.dialog.value = false
                                            vm.selected = []
                                            vm.fetch()
                                        }

                                    } else {
                                        this.$store.commit('error', 'No item has been selected! please select at least one item ')
                                    }
                                }
                            })
                        }
                    }
                }
            })
        }
    ]
}