import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default {
    // title: 'Sub-Affiliate',
    title: {
        singular: 'Sub-Affiliate',
        plural: 'Sub-Affiliates',
    },
    icon: 'mdi-account-network-outline',
    items: {

        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Name',
                align: 'start',
                value: 'name',
            },
            {
                text: 'Affiliate',
                value: 'affiliate.name'
            },
            // { text: 'Active', value: 'active', sortable: false, width: '10%' },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }
        ],
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels: [
                // {
                //     title: 'Active',
                //     fields: [
                //         {
                //             input: 'switch',
                //             name: 'active',
                //             default: true,
                //             bind: {
                //                 label: "Active"
                //             }
                //         }
                //     ]
                // },
                {
                    title: 'Affiliate',
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'affiliate',
                            bind: {
                                label: "Affiliate",
                                collection: 'affiliate',
                                itemText: "name",
                                itemValue: "_id",
                                prependInnerIcon: "mdi-account-network-outline",
                                rules: [
                                    v => !!v || 'Affiliate is required',
                                ],
                                required: true,
                            }
                        }
                    ]
                },
                {
                    title: 'Created By',
                    icon: "mdi-account",
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'subaffiliate')[0].write.allOwn == 'all'),
                            default: vm => vm.user,
                            bind: {
                                label: "User",
                                template: 'user',
                                collection: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                hideAddNew: true,
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Name',
                    fields: [
                        {
                            input: 'text',
                            name: 'name',
                            bind: {
                                label: "Name",
                                rules: [
                                    v => !!v || 'Name is required',
                                ],
                                required: true,
                            }
                        },
                    ]
                },

               
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'affiliate',
                    bind: {
                        label: "Affiliate",
                        collection: 'affiliate',
                        itemText: "name",
                        itemValue: "_id",
                        prependIcon: "mdi-account-network-outline",
                        rules: [
                            v => !!v || 'Affiliate is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'switch',
                    name: 'active',
                    default: true,
                    bind: {
                        label: "Active"
                    }
                }
            ]
        }
    ]
}