

import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default {
    // title: 'Bank Transactions',
    title: {
        singular: 'Bank Transaction',
        plural: 'Banks Transactions',
    },
    icon: 'mdi-bank-transfer',
     
    
    items: {
        total: {
            headers: [
                {
                    text: 'Currency',
                    align: 'start',
                    value: '_id',
                    component: () => import('./../components/items/fields/Currency.vue'),
                    sortable: false,
                    width:"50"
                },
                {
                    text: 'Subtotal',
                    align: 'start',
                    value: 'subtotal.amount',
                    format: 'money',
                    formatArgs: ['$_id', true],
                    sortable: false,
                },
                {
                    text: 'Withraw',
                    align: 'start',
                    format: 'money',
                    formatArgs: ['$_id', true],
                    value: 'withdraw.amount',
                    sortable: false,
                },
                {
                    text: 'Total',
                    align: 'start',
                    format: 'money',
                    formatArgs: ['$_id', true],
                    value: 'amount',
                    sortable: false,
                },
                // {
                //     text: 'Total SAR',
                //     align: 'start',
                //     format: 'money',
                //     formatArgs: ['SAR', true],
                //     value: 'amount_sar',
                //     sortable: false,
                // }
            ]
        },
        buttons: tableButtons.get('new', 'withdraw', 'delete'),
        filters: [


            {
                input: 'autocomplete',
                name: 'bank',
                col: {
                    sm: 7
                },
                bind: {
                    label: "Bank",
                    prependIcon: "mdi-bank",
                    collection: 'bank',
                    itemText: "number",
                    itemValue: "_id",
               
                }
            },
            {
                input: 'range',
                name: 'created_on',
                col: {
                    sm: 5
                },
                bind: {
                    label: "Date",
                    prependIcon: "mdi-calendar",

                }
            }

        ],
        headers: [
            { text: 'Agent', value: 'agent' },
            { text: 'Bank', value: 'bank.number' },
            { text: 'Country', value: 'bank.country', format: 'country', width: '100' },
            { text: 'Currency', value: 'currency', sortable: false, component: () => import('./../components/items/fields/Currency.vue') },
            { text: 'Type', sortable: false, value: 'type', format: 'depositType', width: '50'  },
            // { text: 'Amount', value: 'amount', format: 'money', formatArgs: ['$bank.currency'] },
            { text: 'TRY', value: 'amount_try', format: 'money', formatArgs: ['TRY', true] },
            { text: 'EUR', value: 'amount_eur', format: 'money', formatArgs: ['EUR', true] },
            { text: 'SAR', value: 'amount_sar', format: 'money', formatArgs: ['SAR', true] },
            { text: 'USD', value: 'amount_usd', format: 'money', formatArgs: ['USD', true] },
            { text: 'Date', value: 'created_on', format: 'date' },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }

        ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels:[
                {
                    title: 'Created Date',
                    fields:[
                        {
                            input: 'date',
                            name: 'created_on',
                            default: (new Date()).toISOString().split('T')[0],
                            bind: {
                                label: "Created Date",
                                prependInnerIcon: 'mdi-calendar',
                                required: true,


                                rules: [
                                    v => !!v || 'Created On is required',
                                ],
                            }
                        },
                    ]
                },
                {
                    title: 'Created By',
                    icon: 'mdi-account',
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'banktransaction')[0].write.allOwn == 'all'),
                            default: vm => vm.user,
                            bind: {
                                label: "User",
                                collection: 'user',
                                template: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                required: true,
                                or: false,
                                query: {
                                    role: ['!wakel', '!affiliate'],
                                },
                                rules: [
                                    v => !!v || 'User is required',
                                ],
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Agent',
                    fields: [
                        {
                            input: 'text',
                            name: 'agent',
                            bind: {
                                label: "Agent name",
                                prependInnerIcon: 'mdi-account',
                            }
                        },
                    ]
                },
                {
                    title: 'Bank',
                    fields:[
                        {
                            input: 'autocomplete',
                            name: 'bank',
                            col: {
                                md: 9
                            },
                            bind: {
                                label: "Bank",
                                prependInnerIcon: "mdi-bank",
                                collection: 'bank',
                                itemText: "number",
                                itemValue: "_id",
                                required: true,
                                searchFields: ['number', 'country'],
                                returnObject: true,
                                rules: [
                                    v => !!v || 'Bank is required',
                                ],
                            },
                            on: (vm) => ({
                                input(bank) {
                                    console.log(bank);
                                    vm.data.currency = bank ? bank.currency : ''
                                }
                            })
                        },
                        {
                            input: 'select',
                            name: 'currency',
                            col: {
                                md: 3
                            },
                            bind: {
                                label: "Currency",
                                prependInnerIcon: "mdi-currency-usd-circle-outline",
                                items: ['USD', 'EUR', 'SAR', 'TRY'],
                                required: true,
                                rules: [
                                    v => !!v || 'Currency is required',
                                ],
                            },
                            on: (vm) => ({
                                input(currency) {
                                  
                                    const icons = {
                                        'USD': 'mdi-currency-usd',
                                        'SAR': 'mdi-currency-rial',
                                        'TRY': 'mdi-currency-try',
                                        'EUR': 'mdi-currency-eur',
                                    }

                                    if (currency) {
                                        vm.props['banktransaction_amount'] = {
                                            prependInnerIcon: icons[currency],
                                            label: 'Amount in ' + currency
                                        }

                                    } else if (vm.props['banktransaction_amount'])
                                        delete vm.props['banktransaction_amount']
                                }
                            })
                        },
                    ]
                },
                {
                    title: 'Transaction amount',
                    fields: [
                        {
                            input: 'text',
                            name: 'amount',
                            col: {
                                md: 6
                            },
                            bind: (vm) => ({
                                label: (() => {
                                  

                                    return vm.data.currency ? `Amount in ${vm.data.currency}` : "Amount"
                                })(),
                                prependInnerIcon: (() => {
                                    
                                    const icons = {
                                        'USD': 'mdi-currency-usd',
                                        'SAR': 'mdi-currency-rial',
                                        'TRY': 'mdi-currency-try',
                                        'EUR': 'mdi-currency-eur',
                                    }
                                    return icons[vm.data.currency] || 'mdi-cash'
                                })(),
                                required: true,
                                type: 'number',
                                rules: [
                                    v => !!v || 'Amount is required',
                                ],
                            })
                        },
                        {
                            input: 'text',
                            name: 'amount_sar',
                            col: {
                                md: 6
                            },
                            bind: {
                                label: "Amount in SAR",
                                prependInnerIcon: 'mdi-currency-rial',
                                required: true,
                                type: 'number',
                                rules: [
                                    v => !!v || 'Amount is required',
                                ],
                            }
                        },

                        {
                            input: 'hidden',
                            name: 'type',
                            default() {
                                const urlParams = new URLSearchParams(location.search);
                                return urlParams.get('type') || 'deposit'
                            }
                        },
                    ]
                }
            ]
        }
    } 
}