import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default {
    // title: 'Menu',
    title: {
        singular: 'Menu',
        plural: 'Menu',
    },
    icon: 'mdi-microsoft-xbox-controller-menu',
    items: {
        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Name',
                align: 'start',
                sortable: false,
                value: 'name',
            },
            { text: '', value: 'actions', width: '150', buttons: rowButtons.get() }
        ],
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels: [
                {
                    title: 'Created By',
                    icon: "mdi-account",
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'menu')[0].write.allOwn == 'all'),
                            default: vm => vm.user,
                            bind: {
                                label: "User",
                                template: 'user',
                                collection: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                hideAddNew: true,
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Name',
                    fields: [
                        {
                            input: 'text',
                            name: 'name',
                            bind: {
                                label: "Name",
                                rules: [
                                    v => !!v || 'Name is required',
                                ],
                                required: true,
                            }
                        },
                    ]
                },
                {
                    title: 'Menu Items',
                    fields: [
                        {
                            input: 'menu-items',
                            name: 'menu_item',
                            default: [{ "name": "Dashboard", "icon": "mdi-view-dashboard", "path": "/" },
                            { "name": "Deposit", "icon": "mdi-bank-transfer-in", "path": "/deposit" },
                            { "name": "Customer", "icon": "mdi-account-box", "path": "/customer" },
                            { "name": "Payment Method", "icon": "mdi mdi-credit-card-settings-outline", "path": "/payment_method" },
                            { "name": "Affiliate", "icon": "mdi-account-network-outline", "path": "/affiliate" },
                            { "name": "Users", "icon": "mdi-account-multiple", "path": "/user" },
                            { "name": "Roles", "icon": "mdi-key", "path": "/role" },
                            { "name": "Menus", "icon": "mdi-menu", "path": "/menu" },
                            { "name": "Business", "icon": "mdi-domain", "path": "/business" },
                            { "name": "Department", "icon": "mdi-briefcase-account-outline", "path": "/department" }],
                            bind: {
                                label: 'Menu Items'
                            }
                        } 
                    ]
                }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'menu_item',
                    name: 'menu_item',
                    default: [{ "name": "Dashboard", "icon": "mdi-view-dashboard", "path": "/" }, 
                    { "name": "Deposit", "icon": "mdi-bank-transfer-in", "path": "/deposit" }, 
                    { "name": "Customer", "icon": "mdi-account-box", "path": "/customer" }, 
                    { "name": "Payment Method", "icon": "mdi mdi-credit-card-settings-outline", "path": "/payment_method" }, 
                    { "name": "Affiliate", "icon": "mdi-account-network-outline", "path": "/affiliate" }, 
                    { "name": "Users", "icon": "mdi-account-multiple", "path": "/user" }, 
                    { "name": "Roles", "icon": "mdi-key", "path": "/role" }, 
                    { "name": "Menus", "icon": "mdi-menu", "path": "/menu" }, 
                    { "name": "Business", "icon": "mdi-domain", "path": "/business" }, 
                    { "name": "Department", "icon": "mdi-briefcase-account-outline", "path": "/department" }],
                    bind: {
                         label: 'Menu Items'
                    }
                }
                
            ]
        }
    ]
}